.paginator {
    position: absolute;
    bottom: 10px;
}
.paginator button {
    margin: 0 14px;
    cursor: pointer;
    padding: 2px 8px;
}

.paginator span {
    font-weight: bold;
    color: #102753;
}
@media screen and (max-width: 991px) {
    .paginator {
        bottom: 15px;
    }
}